@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&family=New+Tegomin&display=swap);
body {
  background: #044855;
  margin: 0;
  font-family: 'New Tegomin', serif;
}

.text-center {
  text-align: center;
}
.wrapper {
  padding: 50px 20px;
}

.mb-50 {
  margin-bottom: 50px;
}

.custom-scrollbar::-webkit-scrollbar-track {
	background: rgba(255, 255, 255,0.1);
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar {
	width: 6px;
	background-color: transparent;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
	background-color: #e8b4b0;	
  border-radius: 10px;
}

h1 {
  font-family: 'Cabin Sketch', cursive;
  font-size: 55px;
  font-weight: normal;
  color: #fff;
  letter-spacing: 1px;
  display: inline-block;
  margin: 0 0 25px;
}

p {
  font-size: 24px;
  color: #fff;
}
h3 {
  font-size: 20px;
  margin: 1rem 0 0.6rem;
  color: #1e3b70;
}

p {
  font-size: 18px;
  margin: 0;
  color: #000;
  line-height: 1;
  word-break: break-all;
}

.card {
  background: #ecd1cd;
  margin: 0.8rem;
  padding: 1rem 1.5rem 2rem;
  flex: 1 0 20%;
  max-width: 280px;
  box-shadow: 5px 5px 15px 0 rgb(0, 0, 0, 0.2);
  border-radius: 0.5rem;
  text-align: center;
}

img {
  max-width: 100%;
}

.grow {
  transition: transform .25s ease-out,-webkit-transform .25s ease-out;
}

.grow:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.card_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.search {
  -webkit-appearance: none;
  border: none;
  outline: none;
  font-size: 16px;
  padding: 10px 20px;
  width: 405px;
  border-radius: 0.5rem;
  line-height: 30px;
  margin-bottom: 30px;
  display: block;
  margin: 0 auto;
}

